.navbar {
	list-style: none;
	display: flex;
	background-color: #000000;
}

.navitem {
	margin: 8px;
	color: aqua;
}
body {
	color: #222;
}
.blogmain {
	padding: 20px;
	max-width: 700px;
	margin: 0 auto;
}

.blogheader {
	display: flex;
	justify-content: space-between;
	margin-top: 20px;
	margin-bottom: 50px;
	align-items: center;
}
header a {
	text-decoration: none;
	color: inherit;
}

header a.logo {
	font-weight: bold;
	font-size: 1.5rem;
}
.blognav {
	display: flex;
	gap: 15px;
}
div.post {
	display: grid;
	grid-template-columns: 0.9fr 1.1fr;
	gap: 20px;
	margin-bottom: 30px;
}

div.post div.texts h2 {
	margin: 0;
	font-size: 1.8rem;
}
div.post p.info {
	margin: 6px 0;
	color: #888;
	font-size: 0.7rem;
	font-weight: bold;
	display: flex;
	gap: 10px;
}
div.post p.info a.author {
	color: #333;
}
div.post p.summary {
	margin: 10px 0;
	line-height: 1.4rem;
}
